<template>
  <!--  <div class="p-py-3 p-px-3" v-if="$store.state.breadcrumb.length > 0">-->
  <!--    <Breadcrumb :home="home" :model="$store.state.breadcrumb" />-->
  <!--  </div>-->
  <div class="p-grid p-nogutter p-px-3 p-pt-3">
    <div class="p-col-12 p-lg-6 p-lg-offset-3 p-xl-4 p-xl-offset-4 p-col-nogutter">
      <form @submit.stop.prevent="addPost">
        <Card>
          <template #title>
            Post qo'shish
          </template>
          <template #content>
            <div class="p-fluid">
              <div class="p-field">
                <label>Category</label>
                <Dropdown v-model="v$.form.category.$model"
                          :options="categories"
                          optionLabel="name_uz"
                          optionValue="id">
                  <template #option="slotProps">
                    <p class="p-m-0" @click="getFilters(slotProps.option.id)">{{ slotProps.option.name_uz }}</p>
                  </template>
                </Dropdown>
                <ValidationErrors :form="v$.form" attribute="category"/>
              </div>
            </div>
            <div class="p-fluid">
              <div class="p-field">
                <label>Filters</label>
                <MultiSelect v-model="selectedCountries"
                             :options="countries"
                             optionLabel="name"
                             placeholder="Select Countries"
                             :filter="true"
                             class="multiselect-custom"
                             :disabled="form.category === null">
                  <template #value="slotProps">
                    <div class="country-item country-item-value" v-for="option of slotProps.value" :key="option.code">
                      <div>{{option.name}}</div>
                    </div>
                    <template v-if="!slotProps.value || slotProps.value.length === 0">
                      Select filters
                    </template>
                  </template>
                  <template #option="slotProps">
                    <div class="country-item">
                      <div>{{slotProps.option.name}}</div>
                    </div>
                  </template>
                </MultiSelect>
              </div>
              <div class="p-field">
                <label>Subject</label>
                <InputText v-model="v$.form.subject_uz.$model"
                           :class="{'p-invalid': v$.form.subject_uz.$dirty && v$.form.subject_uz.$invalid}"
                           :disabled="$store.state.loading"/>
                <ValidationErrors :form="v$.form" attribute="subject_uz"/>
              </div>
              <div class="p-field">
                <label>Content</label>
                <Textarea v-model="v$.form.content_uz.$model"
                          :class="{'p-invalid': v$.form.content_uz.$dirty && v$.form.content_uz.$invalid}"
                          :disabled="$store.state.loading"/>
                <ValidationErrors :form="v$.form" attribute="content_uz"/>
              </div>
              <div class="p-field">
                <label>Status</label>
                <Dropdown v-model="v$.form.status.$model"
                          :options="statusList"
                          optionLabel="name"
                          optionValue="value"/>
                <ValidationErrors :form="v$.form" attribute="status"/>
              </div>
            </div>
            <div class="p-fluid">
              <div class="p-field">
                <label>Files</label>
                <UploadButton/>
              </div>
            </div>
          </template>
          <template #footer>
            <Button icon="pi pi-check" :label="$t('save')" type="submit"
                    :loading="$store.state.loading"
                    :disabled="$store.state.loading"/>
          </template>
        </Card>
      </form>
    </div>
  </div>
</template>


<script>
import {defineComponent} from "vue";
import useVuelidate from "@vuelidate/core";
import ValidationErrors from "@/components/ValidationErrors";
import {required} from "@vuelidate/validators";
import UploadButton from "../../components/UploadButton";

export default defineComponent({
  components: {UploadButton, ValidationErrors},
  setup() {
    return {
      v$: useVuelidate()
    }
  },
  mounted() {
    this.getCategories()
    // this.getFilters(1)
  },
  data() {
    return {
      form: {
        category: null,
        subject_uz: null,
        content_uz: null,
        status: null,
        filters: null,
        files: null
      },
      countries: [
        {name: 'Australia', code: 'AU'},
        {name: 'Brazil', code: 'BR'},
        {name: 'China', code: 'CN'},
        {name: 'Egypt', code: 'EG'},
        {name: 'France', code: 'FR'},
        {name: 'Germany', code: 'DE'},
        {name: 'India', code: 'IN'},
        {name: 'Japan', code: 'JP'},
        {name: 'Spain', code: 'ES'},
        {name: 'United States', code: 'US'}
      ],
      selectedCountries: null,
      statusList: [
        {name: 'ACTIVE', value: 1},
        {name: 'INACTIVE', value: 0}
      ],
      categories: null
    }
  },
  methods: {
    async getCategories() {
      // Kategoriyalar ro'yxatini qaytaradi
      let resp = await this.$api.info.categories.get()
      this.categories = resp.data.categories.data.results
    },
    getFilters(id) {
      // (Chala) kategoriyadan kelib chiqib, filterlar ro'yxatini qaytaradi
      // let resp = await this.$api.info.get(null, {url: 'info/category/filters/' + 1 + '/'})
      // console.log(JSON.stringify(resp.data['-']))
      console.log(id)
    },
    async addPost() {
      // (chala filters) Post qo'shish
      this.v$.form.$touch()
      if (this.v$.form.$error) {
        return;
      }

      let resp = await this.$api.info.posts.post(this.form)
      console.log(JSON.stringify(resp.data))
    }
  },
  validations() {
    return {
      form: this.$setMessages({
        category: {required},
        subject_uz: {required},
        content_uz: {required},
        status: {required}
      })
    }
  }
})
</script>

