<template>
  <FileUpload ref="upload" :disabled="disabled" name="file" mode="basic"
              :choose-label="title"
              :uploadLabel="title"
              auto
              :customUpload="true" @uploader="onUpload"/>

  <div v-for="item in files" :key="item.id">
    <hr/>
    <TreeSelect v-model="item._folder" @change="(e) => item.folder = Object.keys(e)[0]" :options="folders"></TreeSelect>
    <textarea v-model="item.comment"></textarea>
    <Chips v-model="item.tagss" />
    <Button @click="onSave(item)">Save</Button>
  </div>
</template>


<script>
import { defineComponent } from "vue";
import { $axios } from "../plugins/axios";

export default defineComponent({
  props: {
    module: {
      type: String
    }
  },
  data() {
    return {
      disabled: false,
      title: "Choose",
      files: [],
      folders: []
    }
  },
  async mounted() {
    let resp = await this.$api.filecloud.folders.tree.get()
    if (resp.success) {
      this.folders = resp.data.folders
    }
  },
  methods: {
    async onSave(item) {
      let data = Object.assign({}, item)
      if (typeof data.tagss === 'undefined') {
        data.tagss = []
      }

      Object.keys(data).forEach(key => {
        if (key.startsWith("_")) {
          delete data[key]
        }
      });

      let resp = await this.$api.filecloud.update.file_id.put(data, {file_id: data.id})
      alert(JSON.stringify(resp))
    },
    async onUpload(e) {
      this.disabled = true
      this.title = "Check space ..."

      let resp = await this.$api.filecloud.storage.check.post({filesize: e.files[0].size})
      if (resp.fail || resp.error) {
        this.title = "Choose"
        this.$toast.add({severity:'error', summary: 'Upload', detail:resp.data.message, life: 5000});
        this.$emit("onerror")
      } else if (resp.success) {
        this.title = "Uploading ..."

        let formData = new FormData()
        formData.append("file", e.files[0])
        formData.append("module", this.module)

        $axios.post('/filecloud/upload/', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          },
          onUploadProgress: function( progressEvent ) {
            let progress = parseInt( Math.round( ( progressEvent.loaded / progressEvent.total ) * 100 ));
            this.title = `Uploading ... ${progress}%`
          }.bind(this)
        }).then(data => {
          if (data.success) {
            data.data._folder = {[data.data.folder]: true}
            this.files.push(data.data)
            this.$emit("onupload", data.data.id)
          } else {
            this.$emit("onerror")
          }
        }).catch(() => {
          this.$emit("onerror")
        }).finally(() => {
          this.title = "Choose"
        })
      }

      this.title = "Choose"
      this.$refs.upload.clear()
      this.disabled = false
    },
  }
})
</script>